















































import {Component, Vue} from 'vue-property-decorator';
import { VueConstructor } from 'vue';
import TopPage from '@/components/TopPage.vue';
import About from '@/components/About.vue'
import NayutaCPage from "@/components/NayutaCPage.vue";
import NayutaIPage from "@/components/NayutaIPage.vue";
import NayutaNPage from "@/components/NayutaNPage.vue";
import FinalPage from '@/components/FinalPage.vue';
import RunePage from '@/components/RunePage.vue';
import OthersPage from '@/components/OthersPage.vue';
import NavigationCard from "@/components/NavigationCard.vue";

@Component
export default class Main extends Vue{
    currentPage:VueConstructor = TopPage;
    navi = false;
    naviList = ['那由多の軌跡 イラスト','那由多の軌跡 漫画', '那由多の軌跡 小説', 'Final Fantasy', 'Rune Factory', 'その他'] ;

    setTop(){
        this.currentPage = TopPage;
    }
    setAbout(){
        this.currentPage = About;
    }
    setContents(index:number){
        switch(index){
            case 0: this.currentPage = NayutaIPage; break;
            case 1: this.currentPage = NayutaCPage; break;
            case 2: this.currentPage = NayutaNPage; break;
            case 3: this.currentPage = FinalPage; break;
            case 4: this.currentPage = RunePage; break;
            case 5: this.currentPage = OthersPage; break;
        }
        this.navi = false;
    }
}
