class Pages{
    title:string ="";
    private novel: string;
    private description:string = "";
    private genru:string = ""
    model:boolean = false;

    constructor(title:string, novel:string, des:string, genru:string){
        this.title = title;
        this.novel = novel;
        this.description = des;
        this.genru = genru;
    }
    getTitle(){
        return this.title;
    }
    getGenru(){
        return this.genru;
    }
    getNovel(){
        return this.novel;
    }
    getComment(){
        return this.description;
    }
    changeModel(){
        this.model = !this.model;
        console.log(this.model)
    }
}
