







import { Component, Vue, Watch } from "vue-property-decorator";

const wWidth = window.innerWidth
const wHeight = window.innerHeight
const aWidth = wWidth/5;
enum Seazon{SPRING="Spting",SUMMER="Summer",AUTUMN="Autumn" ,WINTER="Winter"}
type color_type= string | CanvasGradient | CanvasPattern


let sp_img: HTMLImageElement | null;
let sm_img: HTMLImageElement | null;
let au_img: HTMLImageElement | null;
let wi_img: HTMLImageElement | null;
class FallBack{
    canvas:any = null;
    ctx:any = null;
    ts:number = 0 ;
    img_size_base = 50;
    img_count = 20;
    images:{
        "x":number,
        "y":number,
        "w":number,
        "spd":number,
    //    "ang":number
    }[] = [];

    init(){
        this.canvas = document.getElementById("back");
        this.canvas.width = wWidth;
        this.canvas.height = wHeight;
        this.canvas.style = "z-index: 0"
        this.ctx = this.canvas.getContext('2d');
        sp_img = new Image();
        sm_img = new Image();
        au_img = new Image();
        wi_img = new Image();
        sp_img.src = require("../assets/spring.png");
        sp_img.onload = ()=>this.imgLoad(this, sp_img)
    }
    
    setImages(){
        for(let i=0 ; i < this.img_count ; i++){
            this.images.push({
                "x": Math.random() * wWidth,
                "y": Math.random() * wHeight,
                "w": Math.random() * 10 + this.img_size_base,
                "spd": Math.random() * 3 + 0.2
            })
        }
    }
    imgLoad(parent:any, img:HTMLImageElement|null){
        parent.setImages()
        for(let i =0 ; i < parent.img_count ; i++){
            this.ctx.drawImage(img, parent.images[i].x, parent.images[i].y, this.images[i].w, this.images[i].w)
        }
        this.ts = window.requestAnimationFrame((ts)=>parent.loopSp(ts, img));
    }
    loopSp(ts:number, img:HTMLImageElement | null){
        this.ctx.clearRect(0, 0, wWidth, wHeight);
        for(let i of this.images){
            i.y += i.spd
            if(i.y > wHeight){
                i.y = 0;
            }
            this.ctx.drawImage(img, i.x, i.y, i.w, i.w)
        }
        this.ts = requestAnimationFrame((ts)=>this.loopSp(ts, img))
    }
    changeSeazon(seazon:Seazon){
        window.cancelAnimationFrame(this.ts)
        if(sp_img == null || sm_img == null || au_img == null || wi_img == null)return
        if(seazon == Seazon.SPRING){
            sp_img.src = require("../assets/spring.png");
            sp_img.onload = () => {this. ts = window.requestAnimationFrame((ts)=>{this.loopSp(ts, sp_img)})}
        }else if(seazon == Seazon.SUMMER){
            sm_img.src = require("../assets/summer.png");
            sm_img.onload = () => {this. ts = window.requestAnimationFrame((ts)=>{this.loopSp(ts, sm_img)})}
        }else if(seazon == Seazon.AUTUMN){
            au_img.src = require("../assets/autumn.png");
            au_img.onload = () => {this.ts = window.requestAnimationFrame((ts)=>{this.loopSp(ts, au_img)})}
        }else if(seazon == Seazon.WINTER){
            wi_img.src = require("../assets/winter.png");
            wi_img.onload = () => {this.ts = window.requestAnimationFrame((ts)=>{this.loopSp(ts, wi_img)})}
        }
    }
}

@Component
export default class Astrolabe extends Vue{
    canvas:any = null;
    context:CanvasRenderingContext2D | null = null;
    astrolabe:DrawAstrolabe = new DrawAstrolabe();
    astrolabe_radius = aWidth*2.1;
    //back fall image
    back_ground = new FallBack();
    current_seazon: Seazon = Seazon.SPRING;
    mounted(){
        this.initCanvas();
        this.canvas.addEventListener('click', this.onClick)
    }
    beforeDestroy(){
        this.canvas.removeEventListener('click',this.onClick)
    }

    initCanvas(){
        this.canvas = document.getElementById('canvas');
        if(this.canvas == null) return;
        this.canvas.width = this.astrolabe_radius;
        this.canvas.height = this.astrolabe_radius;
        this.context = this.canvas.getContext('2d');
        if(this.context != null){
            this.astrolabe.drawBase(this.context, this.astrolabe_radius)
        }
        this.back_ground.init()
    }
    
    // callback
    onClick(e:any){
        // canvasがどこにいてもマウス座標を対応
        const cpos = e.target.getBoundingClientRect();
        const clickX = e.clientX - cpos.left;
        const clickY = e.clientY - cpos.top;
        let drawPos = {drawSelectW: 0,drawSelectH: 0};

        if(this.context == null)return;
        
        this.context.lineWidth = 0;
        if(aWidth - 30 < clickX && clickX < aWidth + 30){
            if(aWidth/7 - 30 < clickY && clickY < aWidth/7 + 30){
                //console.log("spring")
                drawPos = this.setCurrentSeazon(aWidth, aWidth/6.5, Seazon.SPRING);
            }else if(aWidth*1.8 - 30 < clickY && clickY < aWidth*1.8 + 40){
                //console.log("autumn")
                drawPos = this.setCurrentSeazon(aWidth,aWidth*1.85,Seazon.AUTUMN)
            }
        }else if(aWidth -30 < clickY && clickY < aWidth + 30){
            if(aWidth/7 -30 < clickX && clickX < aWidth/7 + 30){
                //console.log("winter")
                drawPos = this.setCurrentSeazon(aWidth/6.5, aWidth, Seazon.WINTER)
            }else if(aWidth*1.8 - 30 < clickX && clickX < aWidth*1.8 + 40){
                //console.log("summer")
                drawPos = this.setCurrentSeazon(aWidth*1.85, aWidth, Seazon.SUMMER)
            }
        }
        let gradation_color = this.context.createRadialGradient(drawPos.drawSelectW, drawPos.drawSelectH, 0, drawPos.drawSelectW, drawPos.drawSelectH, aWidth/4)
        gradation_color.addColorStop(0.0, '#E18568')
        gradation_color.addColorStop(1.0, '#ffffbb')
        this.astrolabe.drawCircle(this.context, drawPos.drawSelectW, drawPos.drawSelectH, aWidth/4.8, 0, 2*Math.PI, gradation_color, false)
        this.context.lineWidth = 1;
    
    }
    setCurrentSeazon(dw:number, dh:number, seazon:Seazon){
        if(this.context == null)
            return {drawSelectW: 0,drawSelectH: 0};
        this.astrolabe.drawBase(this.context, this.astrolabe_radius);
        this.context.lineWidth = 10;
        // switch(seazon){
        //     case Seazon.SPRING:this.fall_color = '#ffddee';break;
        //     case Seazon.SUMMER:this.fall_color = '#aaffbb';break;
        //     case Seazon.AUTUMN:this.fall_color = '#ffccaa';break;
        //     case Seazon.WINTER:this.fall_color = '#eeeeff';break;
        //     default:break; 
        // }
        this.current_seazon = seazon;
        this.back_ground.changeSeazon(seazon)
        return {drawSelectW: dw,drawSelectH: dh};
        
    }
    
}
class DrawAstrolabe{
    x:number = aWidth;
    y:number = aWidth;
    radius:number = aWidth;
    constructor(){}
    drawBase(context:CanvasRenderingContext2D, csize:number){
        context.clearRect(0,0,csize, csize)
        let gradation_color;

        // base color #A09c98 -> #ebecea
        gradation_color = context.createRadialGradient(this.x, this.y, this.radius-this.radius/5, this.x ,this.y, this.radius)
        gradation_color.addColorStop(0.0,'#ebece5')
        gradation_color.addColorStop(1.0,'#b1bda9')
        this.drawCircle(context, this.x, this.y, this.radius, 0, 2*Math.PI, gradation_color, true);
        // 切り抜き(ブレンドモードによってかぶっているところを消す)
        this.drawCircle(context, this.x, this.y, this.radius-this.radius/4, 0, 2*Math.PI, '#ffffff', true, 'destination-out')
        
        // cross
        gradation_color = context.createRadialGradient(this.x, this.y , 0, this.x ,this.y, this.radius/1.5)
        gradation_color.addColorStop(0.0,'#6e7b5e')
        gradation_color.addColorStop(1.0,'#FCF3B2')
        this.drawRect(context, this.x - this.radius/6, this.y/3.5, this.radius/3, this.radius*1.5,gradation_color)
        this.drawRect(context, this.x - this.radius/1.35, this.y - this.radius/6, this.radius*1.5, this.radius/3,gradation_color)
    
        // button
        // spring
        this.createGear(context,this.x, this.y - this.radius/1.2,'#F5DBEA', '#9C8C95')
        // summer
        this.createGear(context,this.x + this.radius/1.2, this.y,'#9AF0D3', '#639987')
        // autumn
        this.createGear(context, this.x, this.y + this.radius/1.2,'#FDE7D4', '#CFA784')
        //winter
        this.createGear(context, this.x - this.radius/1.2, this.y, '#F1F6FF', '#96A4BA')
    }
    createGear(context:CanvasRenderingContext2D, x:number, y:number, lc:string, dc:string){
        const r0 = this.radius/5, r1 = this.radius/5.5, r2 = this.radius/6, r3 = this.radius/3;
        const cf = 2*Math.PI
    
        this.drawCircle(context, x, y , r0, 0, cf, '#dadbd9', true)
        this.drawCircle(context, x, y , r1, 0, cf, '#a09098', false)
        let gradation_color = context.createRadialGradient(x, y, 0, x, y, r3)
        gradation_color.addColorStop(0.0, lc)
        gradation_color.addColorStop(1.0, dc)
        this.drawCircle(context, x, y, r2, 0, cf, gradation_color, true)

    }
    drawCircle(context:CanvasRenderingContext2D, x:number, y:number, r:number, sang:number, eang:number, 
                ccolor:color_type, isFill:boolean, rmode:string='source-over'){
        context.save()
        context.beginPath();
        context.globalCompositeOperation = rmode;
        context.arc(x, y, r, sang, eang);
        if(isFill){
            context.fillStyle = ccolor;
            context.fill();
        }else{
            context.strokeStyle = ccolor;
            context.stroke();
        }
        context.restore()
    }
    drawDiamond(context:CanvasRenderingContext2D){
        context.beginPath();
    }
    drawRect(context:CanvasRenderingContext2D, x:number, y:number, w:number, h:number, ccolor:color_type){
        context.beginPath();
        context.strokeStyle = ccolor;
        context.rect(x, y, w, h);
        context.stroke()
    }
}

