


























import {Vue, Component, Prop} from 'vue-property-decorator'

@Component
export default class ListWithDialog extends Vue {
  @Prop()
  isBlack:boolean = false;
  @Prop()
  listItem!:Pages[];
  @Prop()
  listTitle!:string;
}

export class Pages{
    title:string ="";
    private imgsrc: any;
    private description:string[];
    private genru:string = ""
    model:boolean = false;

    constructor(title:string, imgsrc:any, des:string[], genru:string){
        this.title = title;
        this.imgsrc = imgsrc;
        this.description = des;
        this.genru = genru;
    }
    getTitle(){
        return this.title;
    }
    getGenru(){
        return this.genru;
    }
    getImgsrc(){
        return this.imgsrc;
    }
    getComment(i:number){
        return this.description[i];
    }
    changeModel(){
        this.model = !this.model;
    }
}
